@import url("http://fonts.googleapis.com/css?family=Open+Sans:400,600,300");
.print-row {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  column-gap: 10px;
  font-family: "Open Sans", sans-serif;
  // font-weight: 300;
  text-rendering: optimizeLegibility;
  font-size: 0.9em;

  .print-col {
    width: 50%;

    &:nth-child(1) {
      div {
        &:nth-child(1) {
          img {
            height: 70px;
            // width: 75%;
          }
        }

        &:nth-child(2) {
          font-family: "Open Sans", sans-serif;
          h3 {
            font-family: "Open Sans", sans-serif;
            font-size: 1.3em;
            margin-top: 3px;
          }

          p {
            font-size: 1em;
            margin-top: -7px;
            line-height: 17px;
          }
        }
      }
    }

    &:nth-child(2) {
      text-align: right;
      font-family: "Open Sans", sans-serif;

      h2 {
        font-family: "Open Sans", sans-serif;
        font-size: 2.9em;
        letter-spacing: 3px;
        margin-top: 5px;
      }

      h4 {
        font-size: 1.3em;
        margin-top: -1px;
      }

      div {
        h5 {
          font-size: 1.5em;
        }
        p {
          font-size: 1.6em;
        }
      }
    }
  }
}

.changes-card {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: 20px;
}

@media print {
  .changes-card {
    display: none !important;
    // background-color: red !important;
  }

  .dont-print {
    display: none !important;
  }
}

.editorFlat {
  .NgxEditor {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border: none;
    background: var(--app-color-white);
    font-size: 12px;
  }
}
